.ai-chat-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;
}

.chat-window {
  flex: 1;
  overflow-y: auto;
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 4px;
}

.message {
  margin-bottom: 10px;
  padding: 8px;
  border-radius: 4px;
  max-width: 70%;
}

.message.user {
  background-color: #007bff;
  color: white;
  margin-left: auto;
}

.message.ai {
  background-color: #f1f1f1;
  margin-right: auto;
}

.chat-input {
  display: flex;
  gap: 10px;
}

.chat-input input {
  flex: 1;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.chat-input button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.chat-input button:hover {
  background-color: #0056b3;
}

.loading {
  text-align: center;
  padding: 10px;
  color: #666;
}

.error {
  color: #dc3545;
  padding: 10px;
  text-align: center;
}

.chat-input button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
